*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

#root{
  height: 100vh;
  width: 100vw;
  background-color: steelblue;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
}
.quiz-container{
  width: 70%;
  background-color: rgb(2, 2, 53);
  padding: 30px;
  text-align: center;
}
button{
  color: greenyellow;
  font-size: large;
  margin: 5px auto;
  display: block;
  width: 40%;
  padding: 10px;
  border: 1px dotted ;
  backdrop-filter: blur(10px);
  background-color: rgb(239 239 255 / 30%) ;
}
.current-question{
  font-family: 'Courier New', Courier, monospace;
  font-style: italic;
  margin: 5px;
}
.question{
  margin: 10px;
  font-size: larger;
}
.ans{
  margin-top: 25px;
}
.score-text{
  font-size: 2rem;
  color: yellowgreen;
  font-family: fangsong;
}
@media(max-width:530px){
  button{
    width: 70%;
  }
  .quiz-container{
    width: 80%;
  }
}